import React from "react";
import "./footer.css";
import IconFooter from "./iconFooter";
//t lm thm phn footer n gin  nhn trang web cho  trng tri
const Footer = () => {
  //click vo tile  iu hng n Browse
  const clickHome = () => {
    window.location.replace("/");
  };
  return (
    <React.Fragment>
      <div className="footer">
        <h1 className="titleFooter" onClick={clickHome}>
          Movie App
        </h1>
        <div className="flexFooter">
          {/* iu hng trang search */}
          <IconFooter />
          {/* ln u trang */}
          <a href="#top"> Back To Top</a>
        </div>
      </div>
    </React.Fragment>
  );
};
export default Footer;
